<template>
    <div>
        <Topo></Topo>

        <section style="padding: 200px 0">
          <h1>{{ msg }}</h1>
          <v-icon color="blue-grey">mdi-coffee</v-icon> 
          <v-icon>mdi-arrow-right-bold</v-icon>
          <v-icon>mdi-file-code</v-icon>
        </section>

        <Skills></Skills>

        <Rodape></Rodape>
    </div>
</template>

<script>
import Topo from './Topo'
import Skills from './Skills'
import Rodape from './Rodape'

export default {
  name: 'Home',
  data () {
    return {
      msg: 'Transformando café em código!'
    }
  },
  components: {
    Topo,
    Skills,
    Rodape,
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  margin-bottom: 10px;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



